<template>
    <div >
        <div class="suspendClassShow" style="padding-left:108px;margin-bottom:20px;">{{data.courseTableDetailStudent.numberOfChangeClassForTeacher-data.courseTableDetailStudent.allNumberOfChangeClassForTeacher}}{{data.courseTableDetailStudent.allNumberOfChangeClassForTeacher}}</div>
        <el-form  label-width="190px" :model="newItem" :rules="rules" :show-message='false'>
            <el-form-item label="" class="suspendClassShow">
                {{ data.courseTableItem.courseName }}
            </el-form-item>
            <el-form-item label="" class="suspendClassShow">
                {{ data.courseTableItem.duration }}
            </el-form-item>
            <el-form-item label="" class="suspendClassShow">
               {{ data.courseTableItem.date | viewDate }} {{ data.dayOfWeek }} {{ data.courseTableItem.startTime | viewHouAndSec }} ~ {{ data.courseTableItem.endTime | viewHouAndSec }}
            </el-form-item>
           <el-form-item label="" prop='toCourseTableItemId'>
                <el-select v-model="newItem.toCourseTableItemId" style="width:100%;" placeholder="">
                    <el-option v-for="item in data.courseTableItemOther" :key="item.id" 
                       :label=" (item.date +' '+item.startTime + '-' +item.endTime )" :value="item.id"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop='reason'>
                <el-input v-model="newItem.reason"></el-input>
            </el-form-item>
            <el-form-item class="suspendClassShow">
                <el-button @click="cancel()" style="margin-left:40px;"></el-button>
                <el-button type="primary" @click="saveAdd()"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
export default {
    name: 'course-table-switch',
    props: ['id','nStudentId'],
    data() {
        return {
            data: {
                
            },
            newItem: { toCourseTableItemId:'',reason:'' },
            rules: {
                toCourseTableItemId: [
                    { required: true, message: '',trigger: 'blur' },
                ],
                reason: [
                    { required: true, message: '' ,trigger: 'blur'}
                ]
            }
        }
    },
    created(){
        this.getData();
    },
    methods:{
        cancel(){
            this.$emit('cancel');
        },
        getData(){
            this.$http.get('/api/courseTableChangeClassForTeacher/getBeforeChangeClassDate?id='+ this.id+'&nStudentId='+this.nStudentId).then((res) =>{
                if (res.data.code == 1) {
                    console.log(res.data.data)
                     this.data=res.data.data
                } else {
                    console.log(`${res}`)
                }
            });
        },
        saveAdd(){
            this.newItem.fromCourseTableItemId = this.id;
            this.newItem.teacherId = this.data.courseTableItem.teacherId;
            if(!this.newItem.toCourseTableItemId){
                this.$message({
                            message:'',
                            type: 'error'
                        });
                        return
            }
             if(!this.newItem.reason){
                this.$message({
                            message: '',
                            type: 'error'
                        });
                        return
            }
            this.$http.post('/api/courseTableChangeClassForTeacher/addChangeClass', this.newItem).then((res)=>{
                var data = res.data;
                if(data.code == 1){
                    this.$message({
                            message: '',
                            type: 'success'
                        });
                    this.$emit('success');
                }
                else{
                    this.$message({
                            message: data.msg,
                            type: 'error'
                        });
                }
            });
        }
    },
    watch:{
        id(newVal, oldVal){
            this.getData();
            this.newItem= { 
              toCourseTableItemId:'',
              reason:'' 
            }
        }
    },
    components:{
    }
}
</script>
<style lang="less" scoped>
 
</style>



