<template>
    <div >
        <div class="suspendClassShow" style="padding-left:94px;">{{courseTableDetailStudent.allNumberOfChangeClass-courseTableDetailStudent.numberOfleave}}{{courseTableDetailStudent.numberOfleave}}{{courseTableDetailStudent.allNumberOfChangeClass-courseTableDetailStudent.numberOfChangeClass}}{{courseTableDetailStudent.numberOfChangeClass}}</div>
        <div class="suspendClassShow" style="padding-left:92px;font-size:12px;color:red">(=  + )</div>
        <el-form  label-width="175px" :model="newItem"  :rules="rules">
            <el-form-item label=""   class="suspendClassShow">
                {{ sStudentName }}
            </el-form-item>
            <el-form-item label=""   class="suspendClassShow">
                {{ info.courseName }}
            </el-form-item>
            <el-form-item label="" class="suspendClassShow">
                {{ info.duration }}
            </el-form-item>
            <el-form-item label="" class="suspendClassShow">
                {{ info.level }}
            </el-form-item>
            <el-form-item label="" class="suspendClassShow">
                {{ info.number }}
            </el-form-item>
            <el-form-item label="" class="suspendClassShow">
               {{ info.date | viewDate }} {{info.dayOfWeek}} {{ info.startTime | viewHouAndSec }} ~ {{ info.endTime | viewHouAndSec }}
            </el-form-item>
            <el-form-item label="" prop="reason">
                <el-input v-model="newItem.reason"></el-input>
            </el-form-item>
            <el-form-item class="suspendClassShow">
                <el-button @click="cancel()" style="margin-left:40px;"></el-button>
                <el-button type="primary" @click="saveAdd()" ></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
// import post from '../../post'
// import get from '../../get';
// import moment from 'moment';
// import momentlocale from 'moment/locale/zh-cn';
// import lodash from 'lodash';

export default {
    name: 'course-table-switch-for-student',
    props: {
        id : Number, 
        studentId: Number,
        sStatus: String,
        teacherId:Number
    },
    data() {
        return {
            data: {},
            info:{},
            newItem: { 
                reason:'',
                fromCourseTableItemId:''
            },
            rules: {
                currentTeacher: [
                    { required: true, message: '' },
                ],
                toCourseTableItemId: [
                    { required: true, message: '' }
                ],
                reason: [
                    { required: true, message: '' }
                ],
            },
            sStudentName:'',
            courseTableDetailStudent:''
        }
    },
    created(){
        this.getData();
    },
    methods:{
        cancel(){
            this.$emit('cancel');
        },
        getData(){
            this.$http.get('/api/courseTableLeave/getLeaveDataForStudent?id='+ this.id+'&studentId='+this.studentId+'&teacherId='+this.teacherId).then((res) =>{
                var data = res.data;
                this.sStudentName=data.student.name
                this.info = data.info;
                let aDay = ['', '', '', '', '', '', '']
                this.info.dayOfWeek=aDay[new Date(this.info.date).getDay()]
                this.courseTableDetailStudent=data.courseTableDetailStudent
            });
        },
        saveAdd(){
            this.newItem.fromCourseTableItemId = this.id;
            this.newItem.studentId = this.studentId;
            this.newItem.sStatus = this.sStatus;
            if(!this.newItem.reason){
                this.$message({
                            message: '',
                            type: 'error'
                        });
              return
            }
            this.$http.post('/api/courseTableLeave/addLeave', this.newItem).then((res)=>{
                var data = res.data;
                if(data.code == 1){
                    this.$message({
                            message: '',
                            type: 'success'
                        });
                    this.$emit('success');
                }
                else{
                    this.$message({
                            message: data.msg,
                            type: 'error'
                        });
                }
            });
        }
    },
    watch:{
        id(newVal, oldVal){
            this.getData();
        }
    },
    components:{
    }
}
</script>
<style lang="less">
  .el-form .el-form-item.suspendClassShow{
      text-align: left;
      margin-bottom: 10px;
  }
</style>



